.searchLabel {
    display: inline-block;
}
.ui.container {
    padding-bottom: 30px;
}
.compact.selection.dropdown {
    width: 100px;
}


.search-button {
    margin-top: 1em !important;
}


/* tabs */

.ui.inverted.menu .orange.active.item, .ui.inverted.orange.menu {
    background-color: #72aec3  !important;    
}

/* labels */

.ui.label {
    background-color: #72aec3!important;
    border-color: #72aec3!important;
    color: #fff !important;

}

/* list items */
.ui.relaxed.divided.items>.item {
    margin: 1em 0 !important;
    padding: 1.5em 2em !important;
    border: 1px solid rgba(34,36,38,.15) !important;
    border-radius: .28571429rem;
    -webkit-box-shadow: 0 1px 2px 0 rgba(34,36,38,.15);
    box-shadow: 0 1px 2px 0 rgba(34,36,38,.15);
}
.ui.divided.items>.item {
    border-top: 0;
}

.item:first-child {
    border-top: none !important;
}

.item h1 {
    display: inline-block;
}

.ui.items {
    margin: 0;
}

.ui.items>.item .meta {
    font-size: 1.3em;
}

a.header span {
    color: #b9b9b9;
}


.ui.items>.item:nth-child(odd) {
    background: #fff;
}

.ui.items>.item:nth-child(even) {
    background: #f3f3f3;
}


/* sort button */
.dropdown-cont {
    display: inline-block;
    float: right;
    margin-top: 23px;
}

/* input fields */
.eight.wide.column:nth-child(1) > div:nth-child(2),
.eight.wide.column:nth-child(2) > div:nth-child(2) {
    margin-top: 1em;
}
.eight.wide.column:nth-child(2) > .ui.input {
    width: 100%;
}

/* input field error */
.ui.input.fluid.error input {
    background-color: #fff6f6 ;
    border-color: #e0b4b4 ;
    color: #9f3a38 ;
}

/* footer area */
.disc {
    margin-top: 4em;
    border-top: 1px solid rgba(34,36,38,.15);
    text-align: right;
}

.disc {
    padding-top: 10px;
    font-size: 14px;
}
.disc div a {
    margin-left: 5px;
}

/* loader position */
.ui.inline.loader {
    position: absolute;
    vertical-align: middle;
    margin: 0;
    left: 50% !important;
    transform: translateX(-50%) !important;
    top: 50% !important;
    transform: translateY(-50%) !important;
}

/* res design */
@media only screen and (max-width: 767px) {
    .ui.items:not(.unstackable)>.item>.image, .ui.items:not(.unstackable)>.item>.image>img {
        max-width: 100%!important;
        width: auto!important;
        max-height: inherit!important; 
    }
}

@media (max-width: 528px) {
    .ui.pagination.menu a:last-child, 
    .ui.pagination.menu a:first-child,
    .ui.pagination.menu a[type=ellipsisItem] {
        display: none !important;
    }
    .ui.menu .item {
        font-size: 11px;
    }
}

@media (max-width: 400px) {

    .ui.pagination.menu .ui.menu .item {
        font-size: 10px;
    }
}

@media only screen and (max-width: 400px) {
    .search-bar.ui.segment form.ui.form .ui.grid .eight.wide.column {
        display: block !important;
        width: 100% !important;
    }
    .eight.wide.column:nth-child(2) {
        padding-top: 0;
    }
}
